.Searchbar {
    top: 0;
    left: 0;
    width: 100%;
    position: sticky;
    z-index: 1000;
    display: flex;
    justify-content: left;
    align-items: center;
    height: 42px;
    padding-right: 24px;
    padding-left: 24px;
    padding-top: 8px;
    padding-bottom: 8px;
    color: #fff;
    background-color: #b2daf6;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
        0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.SearchForm {
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 600px;
    background-color: #fff;
    border-radius: 3px;
    overflow: hidden;
}

.SearchFormButton {
    display: inline-block;
    width: 36px;
    height: 36px;
    border: 0;
    opacity: 0.6;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
    outline: none;
}

.SearchFormButton:hover {
    opacity: 1;
}

.SearchFormButtonLabel {
position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    clip-path: inset(50%);
    border: 0;
}

.SearchFormButtonIcon{
    width: 24px;
    height: auto;
}

.SearchFormInput {
    display: inline-block;
    width: 100%;
    font: inherit;
    font-size: 20px;
    border: none;
    outline: none;
    padding-left: 8px;
    padding-right: 8px;
}

.SearchFormInput::placeholder {
    font: inherit;
    font-size: 18px;
}